const routes = [
    {
        path: '/',
        redirect: '/components/mosaic',
    },
    {
        path: '/',
        name: 'home',
        component: () => import('@/views/Home.vue'),
        children: [
            {
                path: '/components/mosaic',
                name: 'mosaicPicture',
                component: () => import('@/views/Components/MosaicPicture.vue')
            },
            {
                path: '/components/verification',
                name: 'Verification',
                component: () => import('@/views/Components/Verification.vue')
            },
            {
                path: '/input/emojiFilter',
                name: 'emojiFilter',
                component: () => import('@/views/Input/EmojiFilter.vue')
            },
            {
                path: '/input/marketing',
                name: 'marketing',
                component: () => import('@/views/Input/Marketing.vue')
            }
        ]
    }
]

export default routes